import React, { useCallback, useEffect, useState } from "react";
import ReactDOM from "react-dom";
import IconButton from "@mui/material/IconButton";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputAdornment from "@mui/material/InputAdornment";
import TextField from "@mui/material/TextField";
import { green, red } from "@mui/material/colors";
import Icon from "@mui/material/Icon";
import "./styles.css";
import { Box } from "@mui/material";
import NumberFormatCustom from "./NumberFormatCustom";

function App() {
  const [amount, setAmount] = useState(10000);
  const [targetSplits, setTargetSplits] = useState([20, 30, 50]);
  const [currentSplits, setCurrentSplits] = useState([]);
  const [totalPercent, setTotalPercent] = useState(0);
  useEffect(
    () => setTotalPercent(targetSplits.reduce((acc, c) => acc + c, 0)),
    [targetSplits]
  );
  const onSplitPercentChanges = useCallback(
    (id) => (e) => {
      const newtargetSplits = [...targetSplits];
      newtargetSplits[id] = Number(e.target.value);
      setTargetSplits(newtargetSplits);
    },
    [targetSplits]
  );
  const onTargetSplitValueChanges = useCallback(
    (id) => (e) => {
      if (amount > 0) {
        const { value } = e.target;
        const newtargetSplits = [...targetSplits];
        newtargetSplits[id] = Number((value * 100) / amount);
        setTargetSplits(newtargetSplits);
      }
    },
    [targetSplits, amount]
  );
  const onCurrentSplitValueChanges = useCallback(
    (id) => (e) => {
      const { value } = e.target;
      const newtargetSplits = [...currentSplits];
      newtargetSplits[id] = value;
      setCurrentSplits(newtargetSplits);
    },
    [currentSplits]
  );
  const handleAddtargetSplits = useCallback(() => {
    setTargetSplits((olstargetSplits) => [...olstargetSplits, 0]);
  }, []);

  const handleRemovetargetSplits = useCallback((idx) => {
    setTargetSplits((olstargetSplits) => {
      olstargetSplits.splice(idx, 1);
      return [...olstargetSplits];
    });
  }, []);
  const handleAmountChange = useCallback(
    (e) => setAmount(Number(e.target.value)),
    []
  );
  return (
    <div className="App">
      <h1>Rebalance Calculator</h1>
      <Box
        className="split-section"
        sx={{
          "& > :not(style)": {
            m: 1
          }
        }}
      >
        <TextField
          label="Amount"
          value={amount}
          onChange={handleAmountChange}
          name="numberformat"
          id="formatted-numberformat-input"
          InputProps={{
            inputComponent: NumberFormatCustom
          }}
          variant="standard"
        />
      </Box>
      <Box
        className="split-section"
        sx={{
          "& > :not(style)": {
            m: 1
          }
        }}
      >
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="total">Total</InputLabel>
          <OutlinedInput
            id="total"
            size="small"
            disabled
            value={totalPercent}
            endAdornment={<InputAdornment position="end">%</InputAdornment>}
            label="Total"
            error={totalPercent > 100}
          />
        </FormControl>
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="Total(£)">Total(£)</InputLabel>
          <OutlinedInput
            size="small"
            id="Total(£)"
            label="Total(£)"
            disabled
            value={(amount * totalPercent) / 100}
            name="numberformat"
            inputComponent={NumberFormatCustom}
            variant="standard"
          />
        </FormControl>
        <FormControl fullWidth sx={{ m: 1 }}>
          <InputLabel htmlFor="balance">Balance</InputLabel>
          <OutlinedInput
            size="small"
            id="balance"
            label="Balance"
            disabled
            value={amount - (amount * totalPercent) / 100}
            name="numberformat"
            inputComponent={NumberFormatCustom}
            variant="standard"
          />
        </FormControl>
      </Box>
      {targetSplits.map((split, i) => {
        const splitInAmt = Number((amount * split) / 100);
        return (
          <Box
            key={i}
            className="split-section"
            sx={{
              "& > :not(style)": {
                m: 1
              }
            }}
          >
            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor="outlined-adornment-amount">
                {`Stock-${i + 1}`}
              </InputLabel>
              <OutlinedInput
                size="small"
                id="outlined-adornment-amount"
                value={split.toFixed(0)}
                onChange={onSplitPercentChanges(i)}
                endAdornment={<InputAdornment position="end">%</InputAdornment>}
                label={`Stock-${i + 1}`}
                error={totalPercent > 100}
              />
            </FormControl>

            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`amount${i}`}>£</InputLabel>
              <OutlinedInput
                size="small"
                id={`amount${i}`}
                label="£"
                value={splitInAmt}
                name="numberformat"
                inputComponent={NumberFormatCustom}
                variant="standard"
                onChange={onTargetSplitValueChanges(i)}
              />
            </FormControl>
            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`currntAmount${i}`}>Current£</InputLabel>
              <OutlinedInput
                size="small"
                id={`currntAmount${i}`}
                label="Current£"
                value={Number(currentSplits[i])}
                name="numberformat"
                inputComponent={NumberFormatCustom}
                variant="standard"
                onChange={onCurrentSplitValueChanges(i)}
              />
            </FormControl>
            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`currentPercent${i}`}>Current%</InputLabel>
              <OutlinedInput
                size="small"
                disabled
                id={`currentPercent${i}`}
                label="Current%"
                value={
                  currentSplits[i]
                    ? Number((currentSplits[i] * 100) / amount)
                    : 0
                }
                endAdornment={<InputAdornment position="end">%</InputAdornment>}
              />
            </FormControl>

            <FormControl fullWidth sx={{ m: 1 }}>
              <InputLabel htmlFor={`rebalanceAmount${i}`}>Rebalance</InputLabel>
              <OutlinedInput
                size="small"
                disabled
                id={`rebalanceAmount${i}`}
                label="Rebalance"
                value={
                  splitInAmt - (currentSplits[i] ? Number(currentSplits[i]) : 0)
                }
                startAdornment={
                  <InputAdornment position="start">£</InputAdornment>
                }
              />
            </FormControl>

            <IconButton
              color="secondary"
              onClick={() => handleRemovetargetSplits(i)}
            >
              <Icon fontSize="small" sx={{ color: red[500] }}>
                remove_circle
              </Icon>
            </IconButton>
          </Box>
        );
      })}

      <IconButton
        color="primary"
        aria-label="add to shopping cart"
        onClick={handleAddtargetSplits}
      >
        <Icon sx={{ color: green[500] }}>add_circle</Icon>
      </IconButton>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
